import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { resetUser } from "../../actions/authActions";
import classnames from "classnames";
import { API_URL } from '../../config'

class Resetpassword extends Component {
  constructor() {
    super();
    this.state = {
        email: "",
      password: "",
      password2: "",
      errors: {}
    };
  }

  componentDidMount = () => {
    const { id } = this.props.match.params

    fetch(`${API_URL}/email/confirm/${id}`)
      .then(res => res.json())
      .then(data => {
        console.log("reset",data)
        this.setState({ useremail: data.userEmail });
        this.setState({ confirming: false })
        if(data.userConfirm === true){
          this.props.history.push("/");
        }
        alert(data.msg)
      })
      .catch(err => console.log(err))
  }


  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };

  onSubmit = e => {
    //console.log("Rigister Tapped");
    e.preventDefault();

    const resetUser = {
      email: this.state.useremail,
      password: this.state.password,
      password2: this.state.password2
    };

    this.props.resetUser(resetUser, this.props.history);
  };

  render() {
    const { errors } = this.state;

    return (
      <div className="container">
        <div className="row">
          <div className="col s8 offset-s2">
            <Link to="/login" className="btn-flat waves-effect">
              <i className="material-icons left">keyboard_backspace</i> Back to
              login
            </Link>
            
            <form noValidate onSubmit={this.onSubmit}>
              
              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.useremail}
                  error={errors.email}
                  id="email"
                  type="email"
                  className={classnames("", {
                    invalid: errors.email || errors.emailnotfound
                  })}
                />
                
              </div>
              
              

              <div className="input-field col s12">
                <input
                  onChange={this.onChange}
                  value={this.state.password}
                  error={errors.password}
                  id="password"
                  type="password"
                  className={classnames("", {
                    invalid: errors.password
                  })}
                />
                <label htmlFor="password">Password</label>
                <span className="red-text">{errors.password}</span>
              </div>
              <div className="input-field col s12">
                <input
              
                  onChange={this.onChange}
                  value={this.state.password2}
                  error={errors.password2}
                  id="password2"
                  type="password"
                  className={classnames("", {
                    invalid: errors.password2
                  })}
                />
                <label htmlFor="password2">Confirm Password</label>
                <span className="red-text">{errors.password2}</span>
              </div>
              <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                <button
                  style={{
                    width: "150px",
                    borderRadius: "3px",
                    letterSpacing: "1.5px",
                    marginTop: "1rem"
                  }}
                  type="submit"
                  className="btn btn-large waves-effect waves-light hoverable blue accent-3"
                >
                  Reset password
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

Resetpassword.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { resetUser }
)(withRouter(Resetpassword));
